/* Colors */
$backgroundColor: #fff;
$primaryColor: #2065AB;

// Button
$btnBackgroundColor: $primaryColor;
$btnHoverBackgroundColor: #1F2147;
$btnActiveBackgroundColor: #1F2147;
$btnPadding: 10px 15px 12px;

$textColor: #1F2147;
$lightTextColor: #808080;
$colorRed: #E43136;
$navbarTopIndent: 3.125rem;
$smallFontSize: 15px;
$fontSize: 18px;
$headerHeight: 100px;
$fontFamily: -apple-system,BlinkMacSystemFont, 'Inter', "Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";

$tagsTextColor: #fff;
$tagsBackgroundColor: #8ED8F8;

$inputBackgroundColor: #EEF8FD;

* {
  outline: 0 !important;
}

html {
  font-size: $fontSize;
}

body {
  background-color: $backgroundColor;
  color: $textColor;
  font-family: $fontFamily;
  font-size: $fontSize;
  line-height: 1.5rem;
  overflow-x: hidden;
  position: relative;
}

section {
}

h1 {
  font-size: 32px;
  line-height: 110%;
  margin-bottom: 20px;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 30px;
}

h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 22px;
}

a {
  color: $btnBackgroundColor;
  text-decoration: none;
  font-weight: 400;
  p {
    color: $textColor;
  }

  &:hover {
    color: $btnActiveBackgroundColor;
    text-decoration: none;
  }
}

p {
  margin-bottom: 1.6em;
  line-height: 22px;
  &:last-child {
    margin-bottom: 0;
  }
}

hr {
  margin: 0 50px;
  .container-fluid & {
    margin: 0 35px;;
  }
}


/* Navbar */

.navbar {
  // background-color: $backgroundColor;
  width: 100%;
  padding: 43px 0 .5rem 0;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;

  &.sticky {
    background: #fff;
    padding-top: 0.5rem;
  }

  .container-fluid {
    padding: 0 50px;
  }

  .nav-item {
    white-space: nowrap;
  }
}

.navbar-nav {
  margin-left: auto;
}
.navbar-nav .nav-link {
  color: $textColor;
  font-size: 16px;
  position: relative;

  &:hover,
  &.active {
    color: $lightTextColor;
  }
}

.navbar-collapse {
  font-size: $smallFontSize;
}
/* Navbar end*/

.logo {
  width: 294px;
}

.btn {
  border-radius: 0;
  padding: $btnPadding;
  line-height: 21px;
  font-weight: 500;
}

.btn-primary {
  background-color: $btnBackgroundColor;
  border-color: $btnBackgroundColor;

  &:hover {
    background-color: $btnHoverBackgroundColor;
    border-color: $btnHoverBackgroundColor;
  }

  &:not(:disabled):not(.disabled).active,
  &:not(:disabled):not(.disabled):active {
    background-color: $btnActiveBackgroundColor;
    border-color: $btnActiveBackgroundColor;
  }
  
  &.focus,
  &:focus {
    box-shadow: none;
  }
}

.light-text,
.modal h5 {
  color: $lightTextColor;
  font-size: $smallFontSize;
  line-height: 18px;
}

.container,
.navbar .container {
  padding-right: 20px;
  padding-left: 20px;
}

.container {
  max-width: 1340px;
}

// modal

.modal:not(.mini) {
  background: $backgroundColor;

  .modal-dialog {
    width: 100vw;
    max-width: 100vw;
    margin: $headerHeight 0 0;
    border: none;
  }

  .modal-body {
    padding: 0;
  }

  .modal-content {
    border: none;
    border-radius: 0;
    min-height: calc(100vh - #{$headerHeight});
  }
}

.modal-content {
  background: $backgroundColor;
}

.modal-header {
  border-bottom: none;
  padding: 40px 40px 25px;
}

.modal-body {

  .projects & .item-img {
    min-width: 90px;
    margin-right: 20px;
    text-align: center;
  }
  .projects & ul.advantages-items  {
    list-style: none; /* Remove HTML bullets */
    padding: 0;



    li ul {
      padding: 0 0 0.5em 2em;
    }

    li {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 20px;
      min-height: 130px;
    }
  }

  .vacancy-items & ul {
    list-style: none;
    padding: 0;
    margin-bottom: 40px;
    line-height: 22px;
    li {
      padding-bottom: 10px;
    }
  }
  // .vacancy-items & h1 {
  //   margin-top: 40px;
  // }
}

.main-build {
  background-image: url('/img/home1.png');
  min-height: 300px;
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
}

@media (min-width: 576px) {

}

.modal-footer {
  border-top: 1px solid #dee2e6;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.modal-backdrop.show {
  opacity: 0;
}

.modal.fade .modal-dialog {
  transition: transform 0.3s ease-out;
  transform: none;
}

@media (min-width: 576px) {
  .modal-dialog {
    max-width: 630px;
  }
}

// modal end


.page-footer {
  padding: 60px 0 56px;
  font-size: 16px;
  line-height: 19px;
}

.nav-close {
  margin: 100px 70px;

  svg {
    fill: white;
  }
}

$plus: 70px;


/* HOME */

#company {
  h1 {
    padding-top: 40px;
    margin-bottom: 0.5em;
  }

  p {
    margin-bottom: 40px;
  }
  .container-fluid > .row > div:first-child {
    padding-top: 12vw;
    padding-bottom: 100px;
  }
}

#map {
  height: 337px;
  padding: 0;
}

.bg-blue div,
.bg-light-blue div,
.bg-dark-blue div {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: -1;
}

.bg-blue {
  color: #fff;
  div {
    background-color: #2065AB;
  }
  .services & svg {
    fill: #8BBFE9;
  }
}

.bg-light-blue {
  color: #fff;
  div {
    background-color: #8BBFE9;
  }
  .services & svg {
    fill: #fff;
  }
}

.bg-dark-blue {
  color: #fff;
  div {
    background-color: #1F2147;
  }

  .services & svg {
    fill: #2065AB;
  }
}

#activities svg {
  fill: #8BBFE9;
}

#activities hr,
#vacancy hr {
  margin: 50px 35px 20px;
}
.row .col-xl-4,
.row .col-xl-8,
.row .col-xl-9,
.row .col-lg-4,
.col-12 {
  padding: 20px 50px;
}




.map.row > {
  div:first-child {
    padding-right: 20px;
    padding-top: 10px;
  }
  div:last-child {
    padding-left: 40px;
    padding-top: 10px;
  }
}

.projects > div > a > {
  img {
    margin: -20px -50px 20px;
    width: calc(100% + 100px);
  }

  p {
    margin-bottom: 20px;
  }
}

.services {
  svg {
    position: absolute;
    bottom: 40px;
    right: 45px;
  }

  .col-xl-4 {
    min-height: 292px;
  }
  a:hover {
    color: #fff;
  }
}

.vacancy-item {
  &:hover {
    cursor: pointer;
  }
  span {
    color: $btnBackgroundColor;
  }
}
/* Navbar mobile */
.navbar-dark .navbar-toggler {
  border-color: transparent;
}

.navbar-toggler .icon-bar {
  background-color: $lightTextColor;
  border-radius: 2px;
  display: block;
  height: 3px;
  width: 25px;

  + .icon-bar {
    margin-top: 4px;
  }
}

.icon-bar,
.navbar-toggler .icon-bar,
.navbar-toggler.toggle-clicked .icon-bar {
  transition: all 0.6s ease;
  -webkit-transition: all 0.6s ease;
}

.navbar-toggler.toggle-clicked .icon-bar:nth-child(1) {
  transform: rotate(-43deg);
  margin-left: -27px !important;
  min-width: 31px;
}

.navbar-toggler.toggle-clicked .icon-bar:nth-child(2) {
  transform: rotate(43deg);
  margin-left: -27px !important;
  margin-top: -3px !important;
  min-width: 31px;
}

.navbar-toggler.toggle-clicked .icon-bar:nth-child(3) {
  transform: rotate(37deg);
  margin-left: -1px !important;
  width: 21px !important;
  opacity: 0;
  margin-top: -2px;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.35s ease;
}


// Slider
.projects-slide {
  padding: 0;
}

.projects.row .projects-slide {
  .col-xl-4, .col-xl-8, .col-lg-4 {
    padding: 0;
  }

  p, span {
    padding: 0 50px;
  }
  p {
    margin: 20px 0;
  }
}

.slick-slide img {
  width: 100%;
}

.prevArrow, .nextArrow {
  color: rgba(101, 15, 139, 0.3);
  top: 8vw;
  position: absolute;
  cursor: pointer;
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.prevArrow {
  left: 5px;
  z-index: 1;
}

.nextArrow {
  right: 5px;
}
// Slider end


.page-padding h2 {
  font-size: 22px;
  font-weight: 400;
}

.page-padding h3 {
  font-size: 1rem;
  font-weight: 400;
}

@media (min-width: 576px) {
  
}

@media (min-width: 768px) {
  .navbar + section:not(#company) {
